

















































































































import Vue from "vue";
export default Vue.extend({
  name: "VInlineTimePicker",
  data: () => ({
    internalValue: undefined as any,
    menu2: false,
    uniqueID: "",
    startTime: null as any,
    endTime: null as any,
    showRange: false,
    displayValue: "",
    range: false,
  }),

  props: {
    allowedHours: {
      type: Array,
    },
    allowedMinutes: {
      type: Array,
    },
    dense: {
      type: Boolean,
    },
    rounded: {
      type: Boolean,
    },
    range: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
    },
    label: {
      type: String,
      default: "Pick a time",
    },
    min: {
      type: String,
    },
    max: {
      type: String,
    },
    outlined: {
      type: Boolean,
    },
    readonly: {
      type: Boolean,
    },
    readonlyTextField: {
      type: Boolean,
    },
    rules: {
      type: Array,
    },
    value: {
      type: String,
    },
    willAttach: {
      type: Boolean,
      default: true,
    },
    textFieldClass: {
      type: String,
      default: "",
    },
    _renderingProps: {
      type: Object,
      default: null,
    },
  },
  created() {
    this.uniqueID = this._renderingProps.compCollCb().GetCompUID();
    this.internalValue = this.value;
  },
  mounted() {
    (this.$refs.text as any).visible = true;
    Object.defineProperty(this, "value", {
      get: () => this.internalValue,
      set: (value) => (this.internalValue = value),
    });
    this.resurrect();
  },
  watch: {
    internalValue(newValue) {
      if (newValue !== undefined) {
        this.$emit("change", newValue);
      }
    },
  },
  destroyed() {
    this.$el?.remove();
  },
  methods: {
     resurrect() {
      setTimeout(() => {
        window.dispatchEvent(new Event("resize"));
      }, 200);
    },
    openTimePickers() {
      if (this.range) {
        if(this.value){
        const rangevalues = this.value ? this.value?.split(","):[];
        this.startTime = rangevalues[0];
        this.endTime = rangevalues[1];
      }
        this.showRange = true;
        this.menu2 = true;
      } else {
        this.menu2 = true;
      }
    },
    updateStartTime(newValue) {
      this.startTime = newValue;
      this.internalValue = `${this.startTime},${this.endTime || ""}`;
    },

    updateEndTime(newValue) {
      this.endTime = newValue;
      this.internalValue = `${this.startTime || ""},${this.endTime}`;
    },
    clickMinuteEnd() {
      this.onEndTimeChange();
      if (this.startTime && this.endTime) {
        this.menu2 = false;
      }
    },
    clickMinuteStart() {
      this.onStartTimeChange();
      if (this.startTime && this.endTime) {
        this.menu2 = false;
      }
    },
    onStartTimeChange() {
      this.updateDisplayValue();
      const start = this.startTime.trim() ? this.startTime.trim() : "";
      this.displayValue = `${start},${this.endTime || ""}`;
      this.internalValue = this.displayValue;
    },

    onEndTimeChange() {
      this.updateDisplayValue();
      const end = this.endTime.trim() ? this.endTime.trim() : "";
      this.displayValue = `${this.startTime || ""},${end}`;
      this.internalValue = this.displayValue;
    },
    updateDisplayValue() {
      if (this.range) {
        const start = this.startTime ? this.startTime : "";
        const end = this.endTime ? this.endTime : "";
        this.displayValue = `${start},${end}`;
        //this.internalValue = this.displayValue;
        if (this.startTime && this.endTime) {
          this.menu2 = false;
        }
      } else {
        // Eğer range false ise, sadece tek bir picker görünecek
        const value = this.internalValue ? this.internalValue : "";
      }
    },
    timeChange() {
      this.$emit("change");
    },
    reset() {
      if (
        (this.startTime !== undefined && this.startTime !== null) ||
        (this.endTime !== undefined && this.endTime !== null) ||
        (this.internalValue !== undefined && this.internalValue !== null)
      ) {
        this.startTime = undefined;
        this.endTime = undefined;
        this.internalValue = undefined;
        this.$emit("change", this.internalValue);
      } else {
        this.startTime = undefined;
        this.endTime = undefined;
        this.internalValue = undefined;
      }
    },
    resetValidation() {
      (this.$refs.text as any).resetValidation();
    },
    validationRefs() {
      return [this.$refs.text];
    },
    clickHour(hour) {
      this.$emit("click:hour", hour);
    },
    autoFormatTime(time: string): string {
      if (!time) return ""; // Eğer time boşsa, boş string döndür

      return time
        .split(",")
        .map((part) => {
          const [hours, minutes] = part.trim().split(":");
          const formattedHours = hours ? hours.padStart(2, "0") : ""; // Eğer hours yoksa, varsayılan olarak "00" kullan
          const formattedMinutes = minutes ? minutes.padStart(2, "0") : ""; // Eğer minutes yoksa, varsayılan olarak "00" kullan
          return `${formattedHours}:${formattedMinutes}`; // Her zaman için formatı döndür
        })
        .filter(Boolean)
        .join(", ");
    },
    updateTimeValues(value: string): void {
      const [start, end] = value.split(",").map((time) => time.trim());
      this.startTime = start;
      this.endTime = end || "";
      this.internalValue = value; // Metin alanını güncelle
    },
    onKeyDown(event: KeyboardEvent) {
      if(this.readonlyTextField || this.readonly){
        return;
      }
      const input = event.target as HTMLInputElement;
      const currentValue = input.value;
      const { key } = event;

      const allowedKeys = ["Backspace", "Tab", "ArrowLeft", "ArrowRight"];
      if (allowedKeys.includes(key)) {
        // Backspace durumunda formatı kontrol et
        if (key === "Backspace") {
          // Hemen güncelleme yap
          input.value = currentValue.slice(0, -1); // Son karakteri sil
          this.updateTimeValues(input.value); // Güncellenmiş değeri ayarla
          event.preventDefault(); // Varsayılan davranışı engelle
        }
        return;
      }
      const isValidTime = (hours: string, minutes: string): boolean => {
        return (
          (!hours || (Number(hours) >= 0 && Number(hours) <= 23)) &&
          (!minutes || (Number(minutes) >= 0 && Number(minutes) <= 59))
        );
      };
      const autoFormatTime = (time: string): string => {
        return time
          .split(",")
          .map((part) => {
            const [hours, minutes] = part.trim().split(":");
            const formattedHours = hours?.padStart(2, "0") || "";
            const formattedMinutes = minutes?.padStart(2, "0") || "";
            return formattedHours && formattedMinutes
              ? `${formattedHours}:${formattedMinutes}`
              : formattedHours;
          })
          .filter(Boolean)
          .join(", ");
      };

      if (this.range) {
        // Yalnızca rakamlar, iki nokta ve virgül izinli
        if (!/^[0-9:,]$/.test(key)) {
          event.preventDefault();
          return;
        }
        // Başlangıçta iki nokta veya virgül engelle
        if (currentValue.length === 0 && (key === ":" || key === ",")) {
          event.preventDefault();
          return;
        }
        const colonCount = (currentValue.match(/:/g) || []).length;
        const commaCount = (currentValue.match(/,/g) || []).length;
        // Virgül yalnızca tam bir saat (01:34) girildikten sonra kullanılabilir
        if (
          key === "," &&
          (commaCount >= 1 ||
            colonCount < 1 ||
            !/^\d{1,2}:\d{2}$/.test(currentValue))
        ) {
          event.preventDefault();
          return;
        }
        // Virgülden sonra iki nokta gelmesini engelle
        if (
          key === ":" &&
          commaCount === 1 &&
          !currentValue.split(",")[1]?.trim()?.match(/^\d+$/)
        ) {
          event.preventDefault();
          return;
        }
        // İki nokta sayısını sınırla
        if (colonCount >= 2 && key === ":") {
          event.preventDefault();
          return;
        }
        if (this.startTime && this.endTime) {
          const [startHour] = this.startTime.split(":").map(Number);
          const [endHour] = this.endTime.split(":").map(Number);

           if (startHour > endHour) {
             event.preventDefault();
      }
     }
        const newValue = currentValue + key;
        // Virgülden önce ve sonra geçerli zaman kontrolü
        const [start, end] = newValue.split(",").map((time) => time.trim());
        const [startHours, startMinutes] = start?.split(":") || [];
        const [endHours, endMinutes] = end?.split(":") || [];
        if (
          !isValidTime(startHours, startMinutes) ||
          (end && !isValidTime(endHours, endMinutes)) ||
          !/^\d{0,2}(:\d{0,2})?(,\s*\d{0,2}(:\d{0,2})?)?$/.test(newValue)
        ) {
          event.preventDefault();
          return;
        }
        if (start && end) {
          const startDateTime = new Date(`1970-01-01T${start}:00.000Z`);
          const endDateTime = new Date(`1970-01-01T${end}:00.000Z`);
          if (endDateTime < startDateTime) {
            // End time start time'dan küçükse dakika textfield'a reset at
            this.endTime = "";
            this.internalValue = `${this.startTime},`;
            event.preventDefault();
            return;
          }
        }
        this.startTime = start;
        this.endTime = end ? autoFormatTime(end) : "";
        return;
      } else {
        // Range kapalı olduğunda geçerli giriş kontrolü
        if (currentValue.length === 0 && key === ":") {
          event.preventDefault();
          return;
        }
        if (!/^[0-9:]$/.test(key)) {
          event.preventDefault();
          return;
        }
        if ((currentValue.match(/:/g) || []).length >= 1 && key === ":") {
          event.preventDefault();
          return;
        }
        const newValue = currentValue + key;
        const [hours, minutes] = newValue.split(":");
        if (
          (hours && Number(hours) > 23) ||
          (minutes && Number(minutes) > 59)
        ) {
          event.preventDefault();
          return;
        }
        if (!/^\d{0,2}(:\d{0,2})?$/.test(newValue)) {
          event.preventDefault();
        }
       }
     }, 

    onPaste(event: ClipboardEvent) {
      const pastedData = event.clipboardData?.getData("text") || ""; // Tek tanım
      event.preventDefault();

      const isValid = this.range
        ? /^\d{2}:\d{2},\d{2}:\d{2}$/.test(pastedData) //Range true ise "HH:MM,HH:MM" formatı
        : /^\d{2}:\d{2}(:\d{2})?$/.test(pastedData); //Range false ise "HH:MM" formatı

      if (isValid) {
        // Geçerli bir formatta ise, değeri güncelle
        this.updateTimeValues(pastedData);
      }
    },
  },
});
